<template>
  <div class="particulars">
    <div class="buyheader">
      <h1>年度销量</h1>
    </div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/bell' }">产品列表</el-breadcrumb-item>
      <el-breadcrumb-item>年度销量</el-breadcrumb-item>
    </el-breadcrumb>

    <div class="center-bell">
    <el-collapse accordion>
        <el-collapse-item  v-for="item in buys" :key="item.label">
            <template slot="title">
                <i class="el-icon-location-outline"></i> {{item.label}}
            </template>
        <div v-for="item1 in item.children" :key="item1.label" class="label-1">
            <div>
                <router-link to="/many">
                    {{item1.label}}
                </router-link>
            </div>
        </div>
        </el-collapse-item>
    </el-collapse>
    </div>

  </div>
</template>

<script>
import {buys} from '~/data'

export default {
  data() {
    return {
        buys:buys
    };
  },
};
</script>

<style lang="scss" scoped>
.center-bell{
  padding: 15px;
  a:last-child{
    margin-bottom: 0;
  }
  a{
    background: #eee;
    display: block;
    padding: 10px;
    padding-left: 15px;
    margin: 10px 0;
    color: #461b93;
  }
}


</style>



